              <em class="left">公司大事项</em>
              <div class="eventlist-title-btn" @click="createEvent(-1)"><span class="invested-icon"></span>创建新企业大事</div>
            </div>
            <div class="eventlist-list">
              <div class="eventlist-item1 clear" v-for="item in datalist">
                <div class="eventlist-item1-left left">
                  <img class="eventlist-item1-left-img" :src="item.logo" @error="getImgurl($event)">
                </div>
                <div class="eventlist-item1-right left">
                  <div class="eventlist-item1-right-company" @click="gotoDetail(item.id,item.name)">
                    {{item.name}}
                  </div>
                  <div class="eventlist-item1-right-content clear">
                    <em class="left">最近事项</em><span>{{item.count}}条</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-infinite-scroll="loadMore" infinite-scroll-distance="10">
          </div>
        </div>
      </div>
      <!-- 新建大事日志 -->
      <el-dialog :title="dialogTitle" :visible.sync="createDialogShow" width="570px" center :append-to-body='true' :before-close="dialogClose">
        <div class="dialog_content">
          <div class="form-group margin-bottom-none">
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>标题</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-select v-model="dialogParam.title" filterable clearable reserve-keyword placeholder="可自己创建标题" allow-create :loading="loading">
                    <el-option v-for="item in titleList" :key="item.name" :label="item.name" :value="item.name">
                      <span style="">{{ item.name }}</span>
                    </el-option>
                  </el-select>
                  <!-- <input class="form-group-inputbox-inputtext" type="text" v-model="dialogParam.title"> -->
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>公司名称</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-select v-model="dialogParam.companyVal" filterable :multiple="dialogParam.multiple" clearable remote reserve-keyword placeholder="输入公司名搜索" :remote-method="remoteMethod" :loading="loading" @change="changeCompany($event)">
                    <el-option v-for="item in companyList" :key="item.id" :label="item.name" :value="item.id">
                      <span style="">{{ item.name }}</span>
                      <span style="color: #8492a6; font-size: 13px">{{ item.smallname }}</span>
                    </el-option>
                  </el-select>
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>选择结束时间</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-date-picker type="date" placeholder="选择日期" :editable='false' :clearable='true' value-format="yyyy-MM-dd" class="header-right-time right" v-model="dialogParam.addTime">
                  </el-date-picker>
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title">选择提醒时间<em class="font-color-gray">(提醒时间不能大于结束时间)</em></h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-date-picker type="date" placeholder="选择日期" :editable='false' :clearable='true' value-format="yyyy-MM-dd" class="header-right-time right" v-model="dialogParam.warnTime">
                  </el-date-picker>
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear margin-bottom-none">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title">描述</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <textarea v-model="dialogParam.descrip" class="textarea-normal" name=""></textarea>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" :class="{'btn-blue-disabled':!submitOk}" @click="createEventCommit">确认</button>
          <button class="dialog-btn-submit btn-blue-border" @click="dialogClose">取消</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { bigeventRecentEvent, bigeventCompanyBigEvent, bigeventCompanyList, pubBigevent, happenOrNo } from '../../config/api'
import progressBar from '../../components/progressbar.vue'
import { quillEditor } from 'vue-quill-editor'
import {
  dialogConfirm,
  dialogConfirmHasbtn,
  compareJson
} from '../../config/mUtils'
export default {
  data() {
    return {
      busy: true,
      nodata: false,
      //弹窗
      createDialogShow: false,
      dialogTitle: '创建企业大事',
      totalCompanyList: [],
      companyList: [],
      loading: false,
      titleList: [{
        name: '董事会'
      }, {
        name: '股东会'
      }, {
        name: '年报提交'
      }],
      //弹窗提交参数
      dialogParam: {
        title: '',
        companyId: '',
        companyVal: '',
        addTime: '',
        warnTime: '', //提醒时间
        descrip: '',
        multiple: false
      },
      oldDialogParam: '',
      createType: -1,
      //分页
      number: 10,
      page: 1,
      search: '',
      //近期事件
      recentData: [],
      //公司大事件
      datalist: [],
      total: 0,

    }
  },
  components: {
    progressBar,
    quillEditor
  },
  mounted() {},
  computed: {
    submitOk: function() {
      let param = this.dialogParam;
      if (param.title != '' && param.companyId != '' && param.addTime != '') {
        return true
      } else {
        return false
      }
    }
  },
  created() {
    this.initData();
  },
  methods: {
    initData() {
      this.page = 1;
      this.datalist = [];
      //获取公司近期大事件
      bigeventRecentEvent().then(res => {
        if (res.data.msgNo == 1) {
          let rd = res.data.payload;
          rd.forEach(item => {
            if (item.status == 0) {
              item.checked = false
            } else {
              item.checked = true
            }
            item.time = item.addTime.split(' ');
          })
          this.recentData = rd;
          if (this.recentData.length == 0) {
            this.nodata = true;
          } else {
            this.nodata = false;
          }
        }
      })
      this.getCompanyEventlist();
    },
    //获取公司列表
    getBigeventCompanyList() {
      //获取公司列表
      bigeventCompanyList().then(res => {
        if (res.data.msgNo == 1) {
          this.totalCompanyList = res.data.payload;
          let length = res.data.payload.length;
          this.companyList = res.data.payload;
          if (this.dialogParam.multiple) {
            this.companyList.unshift({
              id: -1,
              name: '全选' + length + '个项目',
              smallname: '（选择此时项目库中的所有项目）'
            });
            this.companyList.unshift({
              id: 0,
              name: '所有项目',
              smallname: '（选择此项包含后面的新添加项目也会自动创建此提醒）'
            })
          }
        }
      })
    },
    getCompanyEventlist() {
      //获取公司大事件
      bigeventCompanyBigEvent({
        number: this.number,
        page: this.page,
        search: this.search
      }).then(res => {
        if (res.data.msgNo == 1) {
          let dl = res.data.payload.data;
          dl.forEach(item => {
            if (item.logo == "") {
              item.logo = "../../static/images/company_headerimg_normal.png"
            }
          })
          this.datalist = this.datalist.concat(res.data.payload.data);
          this.total = res.data.payload.total;
        }
      })
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/company_headerimg_normal.png';
      e.onerror = null;
    },
    //添加标签
    addSpan() {
      if (this.currentspan != '') {
        if (this.commitData.spanlist.indexOf(this.currentspan) == -1) {
          this.commitData.spanlist.push(this.currentspan)
        }
        this.currentspan = '';
      }

    },
    //删除标签
    deleteSpan(index) {
      this.commitData.spanlist.splice(index, 1)
    },
    addFile(e) {},
    //创建大事件
    createEvent(id) {
      this.createType = id;
      this.dialogParam = {
          title: '',
          companyId: '',
          companyVal: '',
          addTime: '',
          warnTime: '',
          descrip: '',
          multiple: true
        },
        this.createDialogShow = true;
      //创建新的大事件
      if (id == -1) {
        this.dialogTitle = '创建新企业大事';
        this.dialogParam.companyId = [];
        this.dialogParam.companyVal = [];
      } else {
        this.dialogTitle = '编辑企业大事';
        let cdl = this.recentData[id];
        if (cdl.companyId == undefined) {
          cdl.companyId = this.companyId
        }
        if (cdl.name == undefined) {
          cdl.name = this.companyName
        }
        this.dialogParam = {
          id: cdl.id,
          status: cdl.status,
          title: cdl.title,
          companyId: cdl.companyId,
          companyVal: cdl.name,
          addTime: cdl.addTime,
          warnTime: cdl.warnTime,
          descrip: cdl.descrip,
          multiple: false
        };
      }
      this.getBigeventCompanyList();
      this.oldDialogParam = JSON.parse(JSON.stringify(this.dialogParam));
    },
    //公司搜索
    remoteMethod(query) {
      if (query != '') {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.companyList = this.totalCompanyList.filter(item => {
            return item.name.indexOf(query) > -1;
          });
        }, 200);
      } else {
        this.companyList = this.totalCompanyList;
      }
    },
    //提交新建大事件
    createEventCommit() {
      if (this.submitOk) {
        if (this.dialogParam.warnTime != '' && (Date.parse(this.dialogParam.warnTime) > Date.parse(this.dialogParam.addTime))) {
          dialogConfirm('info', '提醒时间不能大于结束时间')
        } else {
          if (this.dialogParam.descrip == undefined) {
            this.dialogParam.descrip = '';
          }
          if (this.dialogParam.warnTime == undefined) {
            this.dialogParam.warnTime = '';
          }
          if (this.createType == -1) {
            pubBigevent({
              title: this.dialogParam.title,
              companyId: this.dialogParam.companyId.toString(),
              addTime: this.dialogParam.addTime,
              warnTime: this.dialogParam.warnTime,
              descrip: this.dialogParam.descrip
            }).then(res => {
              if (res.data.msgNo == 1) {
                dialogConfirm('success', '提交成功！');
              } else {
                dialogConfirm('fail', '提交失败！');
              }
              this.initData();
              this.createDialogShow = false;
              this.dialogConfirm = {
                title: '',
                companyId: '',
                companyVal: '',
                addTime: '',
                warnTime: '',
                descrip: ''
              }
            })
          } else {
            happenOrNo({
              id: this.dialogParam.id,
              title: this.dialogParam.title,
              companyId: this.dialogParam.companyId,
              addTime: this.dialogParam.addTime,
              warnTime: this.dialogParam.warnTime,
              descrip: this.dialogParam.descrip,
              status: this.dialogParam.status
            }).then(res => {
              if (res.data.msgNo == 1) {
                dialogConfirm('success', '修改成功');
              } else {
                dialogConfirm('fail', '修改失败！');
              }
              this.initData();
              this.createDialogShow = false;
              this.dialogConfirm = {
                title: '',
                companyId: '',
                companyVal: '',
                addTime: '',
                warnTime: '',
                descrip: ''
              }
            })
          }
        }

      } else {
        dialogConfirm('info', '您还有必填项没有填写')
      }
    },
    //下拉加载更多
    loadMore() {
      if (this.datalist.length < this.total) {
        this.page++;
        this.getCompanyEventlist();
      }
    },
    //修改状态
    changeStatus(id, status) {
      if (status) {
        status = 1
      } else {
        status = 0
      }
      happenOrNo({
        id: id,
        status: status
      }).then(res => {

      })

    },
    //跳转详情页
    gotoDetail(id, name) {
      this.$router.push({
        name: 'investedCompanyEventloglist',
        query: {
          id: id,
          name: name
        }
      })
    },
    //修改公司
    changeCompany(i) {
      if (this.dialogParam.multiple == true) {
        let companyVal = [];
        let currentnum = i[i.length - 1]
        if (currentnum == -1) {
          companyVal = [-1]
        } else if (currentnum == 0) {
          companyVal = [0]
        } else {
          for (let item in i) {
            if (i[item] != -1 && i[item] != 0) {
              companyVal.push(i[item]);
            }
          }
        }
        this.dialogParam.companyVal = companyVal;
        this.dialogParam.companyId = companyVal;
      }

    },
    //弹窗关闭前
    dialogClose() {
      if (compareJson(this.oldDialogParam, this.dialogParam)) {
        this.createDialogShow = false;
        this.dialogParam = {};
      } else {
        dialogConfirmHasbtn('确认不保存就关闭吗？').then(res => {
          this.createDialogShow = false;
          this.dialogParam = {};
        }).catch(() => {})
      }
    }
  }
};

</script>
