  }
  .eventlist-item-right-title-text {
    line-height: 24px;
    max-width: 650px;
    margin-top: -4px;
  }
  .eventlist-item-right-title-textline {
    text-decoration: line-through;
  }
  .eventlist-item-right-company {
    color: #3C98E8;
    line-height: 1em;
    margin-top: 12px;
    &>span {
      display: block;
      float: left;
      width: 14px;
      height: 14px;
      margin-right: 14px;
      cursor: pointer;
      background-position: -35px 0px
    }
    &>span:hover {
      background-position: -20px 0px
    }
    .invested-icon-hide {
      background: none;
      cursor: default;
    }
    .eventlist-item-right-title-text {
      color: #3C98E8;
    }
  }
  .eventlist-item-right-content {
    font-size: 14px;
    color: #808080;
    background: #F9F9F9;
    padding: 5px 10px;
    line-height: 18px;
    margin-top: 14px;
    margin-left: 30px;
  }
  .eventlist-item1 {
    padding-bottom: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid #F4F4F4;
    width: 800px;
  }
  .eventlist-item1-left-img {
    width: 50px;
    height: 50px;
    background: #000;
  }

  .eventlist-item1-right {
    padding-left: 14px;
    width: 750px;
    .eventlist-item1-right-company {
      color: #35383A;
      font-size: 16px;
      line-height: 24px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      &:hover {
        color: #3C98E8;
        cursor: pointer;
      }
    }
    .eventlist-item1-right-content {
      margin-top: 8px;
      &>em {
        font-size: 14px;
        line-height: 16px;
        color: #808080;
        line-height: 1em;
      }
      &>span {
        display: inline-block;
        float: left;
        font-size: 14px;
        line-height: 16px;
        background: #F01400;
        margin-left: 10px;
        border-radius: 10px;
        color: #fff;
        font-size: 10px;
        padding: 0px 6px;
      }
    }
  }
}

.nodatabox {
  //padding: 50px 0px;
  text-align: center;
  @include center;
  .nodata-title {
    font-size: 40px;
    color: #35383A;
    font-weight: bold;
    margin-bottom: 14px;
  }
  .nodata-smalltitle {
    font-size: 14px;
    color: #35383A;
    margin-bottom: 30px;
  }
  .nodata-btn {
    width: 170px;
    line-height: 56px;
    margin: 0 auto;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    background: linear-gradient(134.2deg, rgba(60, 152, 232, 1), rgba(60, 152, 232, 1), rgba(74, 144, 226, 1));
    box-shadow: 0px 0px 10px 0px rgba(112, 180, 255, 0.8)
  }
}

.invested-icon {
  width: 20px;
  height: 20px;
  background: url('../../images/investedcompanyeventlog_icon.png') no-repeat
}

.icon-event {
  background-position: 0px -20px;
}



















/* 弹窗样式 */

.form-group-inputbox {
  width: 470px;
  margin-right: 0px;
}

.form-group-inputboxgroup {
  margin-bottom: 25px;
}

.btn-cancle {
  /* border: 1px solid #3C98E8;
  color: #3C98E8;
  margin-right: 20px; */
}

.dialog-btn-submit.btn-blue-border {
  margin-right: 20px;
}

</style>



